export default {
	data() {
		return {
			
		}
	},
	methods: {
		// 再来一单
		chooseAgain() {
			uni.showModal({
				title:'再来一单',
				content:'你确定要再来一单吗？',
				cancelText:'取消',
				success(res) {
					if(res.confirm){
						console.log('11')
					}else if(res.cancel){
						console.log('22')
					}
				}
			})
		},
		// 去评价
		chooseEvaluation() {
			uni.showModal({
				title:'去评价',
				content:'你确定要评价吗？',
				cancelText:'取消',
				success(res) {
					if(res.confirm){
						uni.navigateTo({
							url:'../../../order/finished/finishedDetail/evaluation/evaluation'
						})
					}else if(res.cancel){
						console.log('22')
					}
				}
			})
		}
	}
}
